header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;
}

.header__socials{
    display:flex;
    flex-direction:column ;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left:0;
    bottom:3rem;
}
.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
/*     background: linear-gradient(to right top , rgb(3, 208, 215),rgb(11, 66, 83)); */
    width:20rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 1.5rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    
}

.scroll__down {
    position: absolute;
    right:-2.3rem;
    bottom:5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width:1024px) {
    header{
        height: 68vh;
    }
}
/* small Screen */

@media screen and (max-width:600px) {
    header {
        height: 100vh;
    }
    .header__socials,.scroll__down {
        display: none;
    }
}
